.info, .sliders {
  background: white;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.15);
  margin: 24px;
  padding: 12px 24px;
  outline: none;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 150;
}

.sliders {
  right: 0;
  left: auto;
  min-width: 300px;
}

.legend {
  display: flex;
  flex-direction: column;
}

.legend > div {
  display: flex;
  align-items: center;
  /* margin: -1px 0; */
  line-height: 1.5;
}

.legend > div::before {
  content: '';
  display: block;
  width: 1em;
  height: 1em;
  margin-right: 1em;
  overflow: hidden;
}

.yellow::before {
  background-color: rgba(229, 153, 48, .5);
}
.orange::before {
  background-color: rgb(229, 153, 48);
}
.lightBlue::before {
  background-color: rgba(37, 112, 232, .5);
}
.blue::before {
  background-color: rgb(37, 112, 232);
}

.sliders {
  transform: translateX(0);
  padding: 0;
}
